@font-face {
  font-family: "Opensans";
  font-weight: normal;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "OpensansBold";
  font-weight: bold;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
body {
  min-width: 320px;
  font-family: "OpensansBold", "Arial", "Helvetica", sans-serif;
  font-size: 15px;
  line-height: 20px;
  color: #000000;
  background-color: #ffffff;
}
img {
  max-width: 100%;
  height: auto;
}
.page-main {
  position: relative;
}
.visually-hidden:not(:focus):not(:active),
input[type="radio"].visually-hidden,
input[type="checkbox"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.wrapper {
  width: 260px;
  margin: 0 auto;
}
@media (min-width: 738px) {
  .wrapper {
    width: 670px;
  }
}
@media (min-width: 1150px) {
  .wrapper {
    width: 1050px;
  }
}
.btn {
  display: block;
  padding: 16px 0;
  font-size: 17px;
  line-height: 16px;
  color: #444444;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  box-sizing: border-box;
  border: 2px solid #444444;
  background-color: transparent;
}
.btn:hover {
  color: #ffffff;
  background-color: #444444;
}
.btn:active {
  color: rgba(255, 255, 255, 0.3);
}
.modal {
  z-index: 1;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  display: none;
  background-color: rgba(255, 255, 255, 0.9);
}
.modal__content {
  width: 320px;
  margin-top: 950px;
  padding-bottom: 46px;
  background-color: #ffffff;
  border-top: 1px solid #c7c7c7;
  box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.25);
}
.modal__title {
  width: 160px;
  margin: 0 auto;
  margin-top: 35px;
  font-size: 25px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;
}
.modal__wrapper {
  width: 260px;
  margin: 0 auto;
}
.modal__text {
  font-family: "Opensans", "Arial", "Helvetica", sans-serif;
  line-height: 30px;
  text-align: center;
  color: #444444;
}
.modal__size-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.modal__label,
.modal__btn {
  font-size: 17px;
  line-height: 16px;
  color: #444444;
  text-transform: uppercase;
  text-align: center;
}
.modal__input + .modal__label {
  display: block;
  width: 82px;
  height: 52px;
  padding: 18px 0;
  box-sizing: border-box;
  border: 2px solid #ececec;
}
.modal__input + .modal__label:hover {
  border-color: #c7c7c7;
}
.modal__input + .modal__label:focus {
  outline: 2px auto #4d90fe;
}
.modal__input:checked + .modal__label {
  border-color: #62d1ba;
}
.modal__input:disabled + .modal__label {
  opacity: 0.3;
}
.modal__btn {
  margin-top: 31px;
  width: 100%;
  color: #ffffff;
  background-color: #63d1bb;
  border: none;
}
@media (min-width: 738px) {
  .modal {
    width: 100%;
  }
  .modal__content {
    width: 566px;
    margin-left: 100px;
    border: 1px solid #c7c7c7;
  }
  .modal__title {
    width: 380px;
    font-size: 30px;
    line-height: 57px;
  }
  .modal__wrapper {
    width: 350px;
  }
  .modal__text {
    font-size: 17px;
  }
  .modal__input + .modal__label {
    width: 112px;
  }
}
@media (min-width: 1150px) {
  .modal__content {
    margin-left: 292px;
  }
}
.page-header {
  min-height: 79px;
  border-bottom: 1px solid #ececec;
}
.page-header__logo {
  padding: 22px 0 18px;
  text-align: center;
}
.page-header__logo a[href]:hover {
  opacity: 0.6;
}
.page-header__logo a[href]:active {
  opacity: 0.3;
}
.page-header__menu-toggle {
  position: absolute;
  top: 29px;
  right: 28px;
  display: none;
  width: 25px;
  height: 20.512px;
  border: none;
  background-color: transparent;
  background-image: url("../img/icon-menu-close.svg");
  background-repeat: no-repeat;
  background-size: 25px 20.512px;
}
.page-header__menu-toggle--js {
  display: block;
}
.page-header__menu-toggle--closed {
  top: 32px;
  right: 26px;
  background-image: url("../img/icon-menu-open.svg");
  background-size: 20px 14px;
}
@media (min-width: 738px) {
  .page-header {
    min-height: 50px;
    border-bottom: 1px solid #e7e7e7;
  }
  .page-header__wrapper {
    position: relative;
    display: flex;
    width: 670px;
    margin: 0 auto;
  }
  .page-header__logo {
    width: 321px;
    padding: 22px 0 16px;
    text-align: left;
  }
  .page-header__logo-image {
    width: 112px;
    height: 46px;
  }
  .page-header__menu-toggle {
    display: none;
  }
  .page-header__menu-toggle--js {
    display: none;
  }
}
@media (min-width: 1150px) {
  .page-header {
    position: relative;
    width: 100%;
    margin: 0 auto;
  }
  .page-header__wrapper {
    width: 1050px;
    display: flex;
  }
  .page-header__logo {
    order: 1;
    padding: 8px 0 3px 64px;
  }
  .page-header__logo-image {
    width: 149px;
    height: 73px;
  }
}
.main-nav {
  display: none;
}
.main-nav--opened {
  display: block;
}
.main-nav__additional-links {
  display: none;
}
@media (min-width: 738px) {
  .main-nav {
    z-index: 1;
    position: absolute;
    top: 80px;
    left: 0;
    display: block;
    width: 670px;
  }
  .main-nav__list {
    width: auto;
  }
}
@media (min-width: 1150px) {
  .main-nav {
    width: 430px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .main-nav,
  .main-nav__list {
    position: static;
  }
  .main-nav__list li:first-child {
    width: 184px;
    border-right: 1px solid #ececec;
  }
  .main-nav__list li:nth-child(2) {
    padding-left: 34px;
  }
  .main-nav__additional-links {
    z-index: 1;
    position: absolute;
    top: 106px;
    left: 0;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }
  .main-nav__additional-link {
    margin-right: 62px;
    line-height: 24px;
    text-decoration: none;
    color: #000000;
  }
  .main-nav__additional-link:hover {
    opacity: 0.6;
  }
  .main-nav__additional-link:active {
    opacity: 0.3;
  }
}
.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.site-list__item {
  padding: 26px 0 28px;
  font-size: 17px;
  line-height: 24px;
  border-top: 1px solid #ececec;
}
.site-list__link {
  display: block;
  width: 245px;
  margin: 0 auto;
  padding-left: 75px;
  text-decoration: none;
  color: #000000;
}
.site-list__link[href]:hover {
  opacity: 0.6;
}
.site-list__link[href]:active {
  opacity: 0.3;
}
.site-list__info {
  display: none;
}
@media (min-width: 738px) {
  .site-list {
    display: flex;
  }
  .site-list__item {
    width: 167px;
    font-size: 15px;
    border: none;
  }
  .site-list__link {
    display: inline-block;
    width: auto;
    padding: 0;
  }
  .site-list__info {
    display: block;
    width: 334px;
    font-family: "Opensans", "Arial", "Helvetica", sans-serif;
    color: #444444;
    text-align: right;
  }
}
@media (min-width: 1150px) {
  .site-list {
    margin: 0;
  }
  .site-list__item {
    padding-top: 31px;
    font-size: 17px;
  }
  .site-list__info {
    position: absolute;
    top: 75px;
    right: 0;
    font-size: 15px;
  }
}
.search-and-cart {
  display: none;
}
.search-and-cart--opened {
  display: block;
}
@media (min-width: 738px) {
  .search-and-cart {
    display: flex;
  }
}
@media (min-width: 1150px) {
  .search-and-cart {
    order: 2;
  }
}
.search {
  padding-bottom: 27px;
}
.search__wrapper {
  width: 260px;
  margin: 0 auto;
}
.search__btn {
  width: 18px;
  height: 24px;
  padding: 0;
  font-size: 0;
  vertical-align: middle;
  cursor: pointer;
  border: none;
  background-color: transparent;
}
.search__btn:hover .search__btn-icon {
  fill: #63d1bb;
}
.search__btn:active .search__btn-icon {
  opacity: 0.3;
}
.search__btn-icon {
  width: 17px;
  height: 17px;
  fill: #d3d2d2;
}
.search__input {
  display: inline-block;
  width: 220px;
  padding: 0;
  padding-left: 23px;
  line-height: 24px;
  border: none;
}
.search__input::placeholder {
  color: #000000;
}
@media (min-width: 738px) {
  .search {
    width: 84px;
    padding: 0;
    border-left: 1px solid #e7e7e7;
    border-right: 1px solid #e7e7e7;
    box-sizing: border-box;
  }
  .search__wrapper {
    width: auto;
  }
  .search__btn {
    width: 84px;
    height: 88px;
    background-position: 50% 51%;
  }
  .search__input {
    display: none;
  }
}
.cart__link {
  position: relative;
}
.cart__link:hover .cart__link-icon {
  fill: #63d1bb;
}
.cart__link:active .cart__link-icon {
  opacity: 0.3;
}
.cart__link-icon {
  position: absolute;
  top: 3px;
  left: 28px;
  width: 24px;
  height: 19px;
  fill: #d3d2d2;
}
@media (min-width: 738px) {
  .cart {
    width: 264px;
    padding: 31px 0 22px;
  }
  .cart__link {
    width: auto;
