.to-catalog {
  min-height: 489px;

  background-color: #fbfbfb;
  background-image: url("../img/hero-mobile@1x.webp"), url("../img/hero-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: 50% -18px;
  background-size: 575px 373px;

  @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
    background-image: url("../img/hero-mobile@2x.webp"), url("../img/hero-mobile@2x.jpg");
  }
}

.to-catalog__title {
  margin: 0;
  padding-top: 70px;
  display: block;

  font-size: 27px;
  line-height: 30px;
  text-align: center;

  & span {
    display: block;
  }
}

.to-catalog__links {
  .no-style();

  margin-top: 122px;
}

.to-catalog__link {
  position: relative;

  display: block;
  width: auto;
  padding: 25px 28px 30px;

  font-size: 19px;
  line-height: 24px;
  color: #ffffff;
  text-decoration: none;

  & p {
    width: 260px;
    margin: 0 auto;
    padding: 0;
  }

  & span {
    display: block;
  }

  &--interior {
    background-color: @green-color;
  }

  &--toys {
    background-color: #56c3b2;
  }

  &:hover {
    padding-bottom: 35px;
  }

  &:hover .to-catalog__link-icon {
    opacity: 0.7;
  }

  &--interior:hover {
    background-color: #59d5bc;
  }

  &--interior:hover .to-catalog__link-icon--interior {
    top: 18px;
  }

  &--toys:hover {
    background-color: #46c1ae;
  }

  &--toys:hover .to-catalog__link-icon--toys {
    top: 16px;
  }

  &:active p,
  &:active .to-catalog__link-icon {
    opacity: 0.3;
  }
}

.to-catalog__link-icon {
  position: absolute;

  fill: #149080;
  opacity: 0.5;

  &--interior {
    top: 23px;
    right: 35px;

    width: 41px;
    height: 62px;
  }

  &--toys {
    top: 21px;
    right: 27px;

    width: 60px;
    height: 60px;
  }
}

@media (min-width: @tablet-width) {
  .to-catalog {
    background-image: url("../img/hero-tablet@1x.webp"), url("../img/hero-tablet@1x.jpg");
    background-size: 1150px 746px;
    background-position: 50% 0;

    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      background-image: url("../img/hero-tablet@2x.webp"), url("../img/hero-tablet@2x.jpg");
    }
  }

  .to-catalog__title {
    margin: 0;
    padding-top: 153px;

    font-size: 43px;
    line-height: 50px;
  }

  .to-catalog__wrapper {
    width: 670px;
    margin: 0 auto;
  }

  .to-catalog__links {
    margin-top: 373px;
    display: flex;
  }

  .to-catalog__links-item {
    width: 50%;
  }

  .to-catalog__link {
    padding-top: 28px;
    padding-bottom: 40px;

    font-size: 20px;
    line-height: 30px;

    &:hover {
      height: 123px;
      padding-top: 23px;
      box-sizing: border-box;
    }

    &--interior:hover .to-catalog__link-icon--interior {
      top: 25px;
    }

    &--toys:hover .to-catalog__link-icon--toys {
      top: 27px;
    }
  }

  .to-catalog__link-icon {
    &--interior {
      top: 30px;
      right: 36px;
    }

    &--toys {
      top: 32px;
      right: 35px;
    }
  }
}

@media (min-width: @desktop-width) {
  .to-catalog {
    background-image: url("../img/hero-desktop@1x.webp"), url("../img/hero-desktop@1x.jpg");

    @media (min-resolution: @retina-dppx), (min-resolution: @retina-dpi) {
      background-image: url("../img/hero-desktop@2x.webp"), url("../img/hero-desktop@2x.jpg");
    }
  }

  .to-catalog__title {
    font-size: 53px;
    line-height: 57px;
  }

  .to-catalog__wrapper {
    width: 1050px;
  }

  .to-catalog__links {
    margin-top: 358px;
  }

  .to-catalog__link {
    padding-left: 37px;

    & p {
      margin: 0;
    }
  }
}
